@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Itim&display=swap');

*{margin:0; box-sizing: border-box; font-family: 'Lato', sans-serif;}
a{text-decoration: none;}
.main_div{width:100%; height:100vh; display: flex; justify-content: center;align-items: center; flex-direction: row; text-align: center; background: chocolate;}
.center_div{width:25%; height: 80vh; border: 1px solid #000; border-radius: 15px; background: darkgray;}
.center_div h1{margin:20px 0; background: darkcyan; font-size: 30px; color:#fff; font-weight: 900; line-height: 45px;}
.center_div input{text-align: center; outline: none; border:none; border-bottom: 3px solid darkcyan; background: darkgray; width:60%;font-size:21.75px; line-height:30px; font-family: 'Itim', cursive; color: rgb(68, 67, 67)}
.center_div button{color:#fff; background: darkcyan; border-radius: 50%; font-weight:900; font-size: 30px; width: 40px; list-style: 40px; margin-left:15px; border: none;box-shadow: 1px 1px 2px 3px rgb(158, 157, 157); cursor: pointer;}
.center_div button:hover{background: rgb(3, 141, 33);}
ol{margin-top: 30px; padding-left: 0;}
ol li{display: flex; align-items: center; text-align: left; font-size: 21px; font-weight: 900; color: rgb(68, 67, 67);}
.style_div{display: flex; justify-content: left; align-items: center; flex-direction: row; margin-bottom: 10px;}
.style_div .fa-times{width:20px ;height: 20px ;background-color:darkcyan; border-radius:50%; color:#fff; display: flex; justify-content: center; align-items: center; margin:5px 15px 0 35px;  cursor: pointer;}
.style_div .fa-times:hover{background-color:red;}
.style_div .fa-times:hover li{color:red;}

@media screen and (max-width:767px){
    .center_div{width:70%}
    .style_div .fa-times{margin:5px 15px 0 20px}
}
@media screen and (max-width:480px){
    .center_div{width:90%}
}
    
